<template>
  <div id="app">
    <div id="drag-toggle">
      <div class="main-panel common-panel flop">
        <div class="t-panel">
          <div class="execute">
            <div id="btn-close" class="item close"><img title="关闭" src="./assets/images/btn_close.png"></div>
            <div id="btn-min" class="item"><img title="最小化" src="./assets/images/btn_min.png"></div>
            <div id="btn-set" class="item"><img title="设置" src="./assets/images/btn_set.png"></div>
          </div>
          <div class="title">
            <img src="./assets/images/qq.png" draggable="false"><p><span class="qq">QQ</span></p>
          </div>
          <div id="anit-out"></div>
        </div>
        <div class="b-panel">
          <div class="avatar-panel">
            <img src="./assets/images/logo.png" draggable="false" alt="avatar">
            <div id="state">
              <div class="state-panel"><img title="在线状态：我在线上" src="./assets/images/state_online.png"></div>
              <div id="state-select"></div>
            </div>
          </div>
          <div class="login-panel">
            <form>
              <div class="input-panel">
                <div class="qq">  
                  <input class="ipt" type="text" name="" placeholder="QQ号码/手机/邮箱" draggable="false">
                  <img src="./assets/images/select_account.png">
                </div>
                <div class="pwd">
                  <input class="ipt" type="password" name="" placeholder="密码" draggable="false">
                  <img src="./assets/images/keyboard.png">
                </div>
              </div>
              <div class="checkbox-panel">
                <div class="check-item1">
                  <input type="checkbox" name="rememberpwd" draggable="false"><span>记住密码</span>
                </div>
                <div class="check-item2">
                  <input type="checkbox" name="autologin" draggable="false"><span>自动登录</span>              
                </div>
              </div>
              <div class="btn-panel">
                <button class="btn" draggable="false">登&nbsp;&nbsp;&nbsp;录</button>
              </div>
            </form>
          </div>
          <div class="service-panel">
            <a href="https://ssl.zc.qq.com/chs/index.html" target="_blank" draggable="false">注册账号</a>
            <a href="https://ssl.ptlogin2.qq.com/forget_pwd?pt_clientver=5457&pt_src=1&ptlang=2052&aquin=" target="_blank" draggable="false">找回密码</a>
          </div>
          <div id="switch-single">
            <img title="多帐号登录" src="./assets/images/switch_single.png">
          </div>
          <div id="qr-code">
            <img title="二维码登录" src="./assets/images/qrcode.png">
          </div>
        </div>
      </div>
      <div class="back-panel common-panel flop">
        <img src="./assets/images/head_1.png">
      </div>
    </div>
  </div>
</template>

<script>
import $ from './assets/js/jquery-v.js'
export default {
  name: 'app'
}
window.onload=(function(){
  drag();
  cClose();
  flop();
});

// QQ close

function cClose() {
  var mainPanel = document.getElementById('drag-toggle'),
      btnClose = document.getElementById('btn-close'),
      timer = null;
    btnClose.onclick=(function() {
      timer = setInterval(function () {
        if(mainPanel.offsetHeight == 0){
          clearInterval(timer);
        }
        mainPanel.style.overflow = "hidden";
        mainPanel.style.height = mainPanel.offsetHeight-10+"px";
        // console.log(mainPanel.offsetHeight);
      },1);
      window.setTimeout(close,200); 
    });
   function close() {
     mainPanel.style.display="none";
   }
}

// QQ flop

function flop() {  
  var mainPanel = document.querySelector('.main-panel'),
      backPanel = document.querySelector('.back-panel'),
      btnSet = document.getElementById('btn-set');
      btnSet.onclick=(function () {
        mainPanel.className +=' main-panel-rotate';
        backPanel.className +=' back-panel-rotate';
      });
}

// QQ drag framework

function drag () {
  var moveArea = document.getElementById('drag-toggle');
  moveArea.onmousedown = fnDown;
}

function fnDown(event){
  event = event;
  var nodeNone = event.target.nodeName;
  var moveArea = document.getElementById('drag-toggle'),
      disX=event.clientX-moveArea.offsetLeft,
      disY=event.clientY-moveArea.offsetTop;
  document.onmousemove = function (event) {
    event = event;
    // console.log(event.target.nodeName);
    fnMove(event,disX,disY);
  }
  if( nodeNone == 'INPUT' || nodeNone == 'IMG' || nodeNone == 'A' || nodeNone == 'BUTTON'){ //阻止在这些标签执行拖动
      document.onmousemove=null;
      document.onmouseup=null;
  }else{}


  document.onmouseup=function () {
    document.onmousemove=null;
    document.onmouseup=null;
  }
}

function fnMove(e, posX, posY){
  var moveArea = document.getElementById('drag-toggle'),
      l=e.clientX-posX,
      t=e.clientY-posY,
      winW=document.documentElement.clientWidth,
      winH=document.documentElement.clientHeight,
      maxW=winW-moveArea.offsetWidth,
      maxH=winH-moveArea.offsetHeight;
      e.stopPropagation();
  if(l<0){
    l=0;
  }else if(l>maxW){
    l=maxW;
  }
  if(t<0){
    t=0;
  }else if(t>maxH){
    t=maxH;
  }
  moveArea.style.left=l+'px';
  moveArea.style.top=t+'px';
}



// QQ LOW poly
$(function() {
  if (!window.ActiveXObject && !!document.createElement("canvas").getContext) {
    $.getScript("http://im-img.qq.com/pcqq/js/200/cav.js",
    // $.getScript("/assets/js/cav.js",
      function() {
        var t = {
          width: 1.5,
          height: 1.5,
          depth: 10,
          segments: 12,
          slices: 6,
          xRange: 0.8,
          yRange: 0.1,
          zRange: 1,
          ambient: "#525252",
          diffuse: "#FFFFFF",
          speed: 0.0005
        };
        var G = {
          count: 2,
          xyScalar: 1,
          zOffset: 100,
          ambient: "#002c4a",
          diffuse: "#005584",
          speed: 0.001,
          gravity: 1200,
          dampening: 0.95,
          minLimit: 10,
          maxLimit: null,
          minDistance: 20,
          maxDistance: 400,
          autopilot: false,
          draw: false,
          bounds: CAV.Vector3.create(),
          step: CAV.Vector3.create(Math.randomInRange(0.2, 1), Math.randomInRange(0.2, 1), Math.randomInRange(0.2, 1))
        };
        var m = "canvas";
        var E = "svg";
        var x = {
          renderer: m
        };
        var i, n = Date.now();
        var L = CAV.Vector3.create();
        var k = CAV.Vector3.create();
        var z = document.getElementById("drag-toggle");
        var w = document.getElementById("anit-out");
        var D, I, h, q, y;
        var g;
        var r;

        function C() {
          F();
          p();
          s();
          B();
          v();
          K(z.offsetWidth, z.offsetHeight);
          o()
        }

        function F() {
          g = new CAV.CanvasRenderer();
          H(x.renderer)
        }

        function H(N) {
          if (D) {
            w.removeChild(D.element)
          }
          switch (N) {
            case m:
              D = g;
              break
          }
          D.setSize(z.offsetWidth, z.offsetHeight);
          w.appendChild(D.element)
        }

        function p() {
          I = new CAV.Scene()
        }

        function s() {
          I.remove(h);
          D.clear();
          q = new CAV.Plane(t.width * D.width, t.height * D.height, t.segments, t.slices);
          y = new CAV.Material(t.ambient, t.diffuse);
          h = new CAV.Mesh(q, y);
          I.add(h);
          var N, O;
          for (N = q.vertices.length - 1; N >= 0; N--) {
            O = q.vertices[N];
            O.anchor = CAV.Vector3.clone(O.position);
            O.step = CAV.Vector3.create(Math.randomInRange(0.2, 1), Math.randomInRange(0.2, 1), Math.randomInRange(0.2, 1));
            O.time = Math.randomInRange(0, Math.PIM2)
          }
        }

        function B() {
          var O, N;
          for (O = I.lights.length - 1; O >= 0; O--) {
            N = I.lights[O];
            I.remove(N)
          }
          D.clear();
          for (O = 0; O < G.count; O++) {
            N = new CAV.Light(G.ambient, G.diffuse);
            N.ambientHex = N.ambient.format();
            N.diffuseHex = N.diffuse.format();
            I.add(N);
            N.mass = Math.randomInRange(0.5, 1);
            N.velocity = CAV.Vector3.create();
            N.acceleration = CAV.Vector3.create();
            N.force = CAV.Vector3.create()
          }
        }

        function K(O, N) {
          D.setSize(O, N);
          CAV.Vector3.set(L, D.halfWidth, D.halfHeight);
          s()
        }

        function o() {
          i = Date.now() - n;
          u();
          M();
          requestAnimationFrame(o)
        }

        function u() {
          var Q, P, O, R, T, V, U, S = t.depth / 2;
          CAV.Vector3.copy(G.bounds, L);
          CAV.Vector3.multiplyScalar(G.bounds, G.xyScalar);
          CAV.Vector3.setZ(k, G.zOffset);
          for (R = I.lights.length - 1; R >= 0; R--) {
            T = I.lights[R];
            CAV.Vector3.setZ(T.position, G.zOffset);
            var N = Math.clamp(CAV.Vector3.distanceSquared(T.position, k), G.minDistance, G.maxDistance);
            var W = G.gravity * T.mass / N;
            CAV.Vector3.subtractVectors(T.force, k, T.position);
            CAV.Vector3.normalise(T.force);
            CAV.Vector3.multiplyScalar(T.force, W);
            CAV.Vector3.set(T.acceleration);
            CAV.Vector3.add(T.acceleration, T.force);
            CAV.Vector3.add(T.velocity, T.acceleration);
            CAV.Vector3.multiplyScalar(T.velocity, G.dampening);
            CAV.Vector3.limit(T.velocity, G.minLimit, G.maxLimit);
            CAV.Vector3.add(T.position, T.velocity)
          }
          for (V = q.vertices.length - 1; V >= 0; V--) {
            U = q.vertices[V];
            Q = Math.sin(U.time + U.step[0] * i * t.speed);
            P = Math.cos(U.time + U.step[1] * i * t.speed);
            O = Math.sin(U.time + U.step[2] * i * t.speed);
            CAV.Vector3.set(U.position, t.xRange * q.segmentWidth * Q, t.yRange * q.sliceHeight * P, t.zRange * S * O - S);
            CAV.Vector3.add(U.position, U.anchor)
          }
          q.dirty = true
        }

        function M() {
          D.render(I)
        }

        function J(O) {
          var Q, N, S = O;
          var P = function(T) {
            for (Q = 0, l = I.lights.length; Q < l; Q++) {
              N = I.lights[Q];
              N.ambient.set(T);
              N.ambientHex = N.ambient.format()
            }
          };
          var R = function(T) {
            for (Q = 0, l = I.lights.length; Q < l; Q++) {
              N = I.lights[Q];
              N.diffuse.set(T);
              N.diffuseHex = N.diffuse.format()
            }
          };
          return {
            set: function() {
              P(S[0]);
              R(S[1])
            }
          }
        }

        function v() {
          window.addEventListener("resize", j)
        }

        function A(N) {
          CAV.Vector3.set(k, N.x, D.height - N.y);
          CAV.Vector3.subtract(k, L)
        }

        function j(N) {
          K(z.offsetWidth, z.offsetHeight);
          M()
        }

        C();
      })
  } else {
    alert('调用cav.js失败');
  }
});

</script>

<style>
@import './assets/font.css';
@import './assets/base.css';

</style>



